import React from 'react';
import { render } from 'react-dom';
import { Toast } from 'antd-mobile';
import { WeaTools } from 'weaCom'
import DetailTable from './components/Table';
import './index.less';

class Main extends React.Component {
	constructor(props){
		super(props);
		this.state = {
			columns: [],
			datas: [],
			dataSource: [],
			current: 0,
			total: 0,
			pageSize: 10,
		}
		this.onChange = this.onChange.bind(this);
	}
	doGet(params){
		const { workflowid, nodeid, requestid } = window.wf__info;
		const { detailNum } = this.props;
		//Toast.loading('...', 0);
		return WeaTools.callApi("/cloudstore/app/no0000012/ControlJersey.jsp","POST",{
		    className:'RestService_WfDetail',
		    wfId: workflowid,
		    nodeId: nodeid,
		    reqId: requestid,
		    detailNum,
		    isSet:1,
		    ...params
		})
	}
	componentDidMount(){
		const { pageSize } = this.state;
		this.doGet({methodName: 'config'}).then(res =>{
			//Toast.hide();
			let columns = [];
			columns.push({
				key: "key",
				dataIndex: "key",
				title: "",
				width:80,
			});
			res.wdfl && res.wdfl.map(col => {
				Number(col.editType) < 3 && columns.push({
					key: col.id,
					dataIndex: col.id,
					title: col.fieldlabelname,
					width: col.width,
				})
			})
			this.setState({columns})
			this.doGet({methodName: 'list',heads: JSON.stringify(res.wdfl), detailtable: res.detailtable}).then(_res =>{
				//Toast.hide();
				let datas = [];
				_res.datas && _res.datas.map((d, i) => {
					let data = { 
						dbId: d.dbId,
						key: i+1,
					};
					d.wdfl.map(col => {
						if(Number(col.editType) < 3){
							data[col.id] = col.valuespan || col.value
						}
					});
					datas.push(data);
				});
				this.setState({ datas, dataSource: this.getDataDource(datas, 0, pageSize), total: Math.ceil(datas.length / pageSize)})
			});
		});
	}
	getDataDource(datas, current, pageSize){
		return datas.filter((data, index) => index >= current * pageSize && index < (current + 1) * pageSize)
	}
	render() {
		return (
	    	<DetailTable {...this.state} onChange={this.onChange}/>
		)
	}
	onChange(current){
		const { datas, pageSize } = this.state;
		this.setState({current, dataSource: this.getDataDource(datas, current, pageSize)})
	}
}

jQuery(document).ready(function(){
	//render(<Main/>, root)
	jQuery('.wfDetail').map((index, dom) => {
		if(dom.id){
			let detailNum = Number(dom.id.split('_')[1]);
			render(<Main detailNum={detailNum} key={detailNum}/>, document.getElementById(dom.id))
		}
	})
});




