import React from 'react';
import { Table, Pagination, Icon } from 'antd-mobile';

const DetailTable = props => {
  const { columns, dataSource, current, total, onChange } = props
  return (
    <div className='wf-detail-mobile-table' style={{width: $('.excelTempDiv').width() || $(window).width() * 0.9}}>
      <Table
        scrollX={true}
        columns={columns}
        dataSource={dataSource}
      />
      {
        total > 0 && <Pagination
          className="wf-detail-mobile-table-pagination"
          current={current}
          total={total}
          onChange={onChange}
          prevText={<Icon type="left" />}
          nextText={<Icon type="right" />}
        />
      }
    </div>
  )
}

export default DetailTable;




